@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.hotToken {
  height: 40px;
  // background: linear-gradient(90deg, rgba(10, 1, 24, 0) 0%, #0a0118 10%, #0a0118 90%, rgba(10, 1, 24, 0) 100%);

  .list {
    .item {
      display: flex;
      align-items: center;
      margin-right: 10px;
      font-family: Poppins;
      line-height: 40px;
      font-size: 12px;
      color: rgba(167, 162, 201, 1);
      cursor: pointer;
      &:hover {
        .number {
          color: #fff;
        }
        .symbol {
          color: #fff;
        }
      }
      .number {
        font-weight: 400;
      }
      .img {
        margin: 0 4px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #fff;
        overflow: hidden;
        img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
      .symbol {
        font-family: Poppins-Bold;
        font-weight: 600;
      }
    }
  }
}
