@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.linkBtn {
  display: inline-flex;
  align-items: center;
  .name {
    margin-right: 10px;
    font-family: Poppins;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: rgba(167, 162, 201, 1);
  }
  .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 22px;
    height: 22px;
    border-radius: 22px;
    background: rgba(54, 37, 78, 1);
    cursor: pointer;
    &:hover {
      background: rgba(97, 80, 132, 1);
      img {
        opacity: 1;
      }
    }
    img {
      display: block;
      width: 11px;
      height: 11px;
      opacity: 0.7;
    }
  }
}

@include mobile-only {
  .linkBtn {
    .icon {
      img {
        opacity: 1;
      }
    }
  }
}
