@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.psm {
  .psm_title {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .list {
    .list_item {
      position: relative;
      margin-top: 10px;
      padding: 20px;
      // height: 128px;
      // background-color: rgba(42, 33, 70, 0.7);
      background: #14052a;
      border-radius: 19px;
      // backdrop-filter: blur(10px);
      border: 1px solid rgba(255, 255, 255, 0.1);
      overflow: hidden;
      &::after {
        // z-index: -2;
        z-index: 1;
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-image: url(../../../assets/images/swap/ico_1.svg);
        background-repeat: no-repeat;
        background-size: auto 128px;
        background-position: right -9px top 39px;
        // opacity: 0.1;
      }
      .list_title {
        font-family: Poppins;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: #fff;
      }
      .list_desc {
        margin-top: 10px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 12px;
        line-height: 24px;
        color: rgba(167, 162, 201, 1);
      }
      .list_icon {
        display: none;
        position: absolute;
        right: 20px;
        top: 20px;
        width: 30px;
        height: 30px;
        background: url(../../../assets/images/swap/ico_1.svg) no-repeat;
        background-size: 100% 100%;
        background-position: left 0 top 0;
      }
      &:nth-child(2) {
        &::after {
          background-image: url(../../../assets/images/swap/ico_2.svg);
        }
        .list_icon {
          background-image: url(../../../assets/images/swap/ico_2.svg);
        }
      }
      &:nth-child(3) {
        &::after {
          background-image: url(../../../assets/images/swap/ico_3.svg);
        }
        .list_icon {
          background-image: url(../../../assets/images/swap/ico_3.svg);
        }
      }
    }
  }
  .footer {
    display: inline-flex;
    align-items: center;
    margin-top: 20px;
    .footer_item {
      margin-right: 30px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.psm-contracts-modal {
  .content {
    max-height: 420px;
    overflow: auto;
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background: transparent;
      border-radius: 10px;
    }

    &:hover::-webkit-scrollbar-thumb {
      background: #dedede;
    }

    &:hover::-webkit-scrollbar-track {
      background: transparent;
    }
    .item {
      text-align: left;
      .item-title {
        font-family: Poppins;
        font-weight: 600;
        font-size: 16px;
        font-weight: 600;
        color: #fff;
        line-height: 24px;
      }
      .ul {
        .li {
          background: rgba(51, 37, 78, 1);
          border-radius: 6px;
          margin-top: 10px;
          padding: 12px;
          position: relative;
          > div {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }
          .name {
            display: flex;
            align-items: center;
            font-family: Poppins;
            font-weight: 600;
            font-size: 16px;
            font-weight: 600;
            color: #fff;
            line-height: 24px;
            & > span:nth-child(1) {
              flex: 1;
            }
          }
          .address {
            margin-top: 8px;
            font-family: Poppins;
            font-weight: 400;
            font-size: 14px;
            font-weight: 600;
            color: rgba(193, 160, 255, 1);
            line-height: 20px;
            word-break: break-all;
          }
          .aaa {
            top: 0;
            right: 0px;
          }
          .contract_wrap {
            display: flex;
            align-items: center;
            width: 58px;
            .contract_icon {
              margin-right: 10px;
              &:last-child {
                margin-right: 0;
              }
            }
          }
          .contract_icon {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 24px;
            height: 24px;
            border-radius: 24px;
            margin-right: 10px;
            background: rgba(70, 58, 98, 1);
            cursor: pointer;
            img {
              display: block;
              width: 16px;
              height: 16px;
              opacity: 0.7;
            }
            &:hover {
              background: rgba(97, 80, 132, 1);
              img {
                opacity: 1;
              }
            }
          }
        }
      }

      .ul-multiple {
        .li {
          .name {
            .contract_wrap {
              display: none;
            }
          }
          .address {
            margin-top: 0;
          }
        }
      }
    }
    .tip {
      font-size: 12px;
      font-family: Poppins;
      font-weight: 400;
      color: #fff;
      line-height: 18px;
      text-align: left;
      margin: 10px 0 20px;
    }
  }
}

@include mobile-only {
  .psm {
    .list {
      .list_item {
        padding: 10px 10px 14px;
        height: auto;
        border-radius: 10px;
        &::after {
          width: 100%;
          height: 100%;
          background-size: auto 98px;
        }
        .list_title {
          font-size: 16px;
          line-height: 24px;
        }
        .list_desc {
          font-size: 12px;
          line-height: 20px;
        }
        .list_icon {
          display: none;
          right: 10px;
          top: 10px;
          width: 24px;
          height: 24px;
        }
      }
    }

    .footer {
      margin-top: 10px;
      margin-bottom: -14px;
      display: inline-flex;
      flex-wrap: wrap;
      .footer_item {
        margin-bottom: 14px;
        margin-right: 30px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  .psm-contracts-modal {
    .content {
      .item {
        .ul {
          .li {
            .name {
              display: inline-flex;
              align-items: start;
              font-size: 14px;
              line-height: 24px;
              & > span {
                &:first-child {
                  margin-right: 10px;
                  flex: initial;
                }
              }
            }
            .address {
              margin-top: 8px;
              a {
                font-size: 12px;
              }
            }
          }
        }
        .ul-multiple {
          .li {
            .name {
              display: block;
              .contract_wrap {
                display: flex;
              }
            }
            .address {
              a {
                font-size: 12px;
              }
              .contract_wrap {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}
