@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.price {
  .title {
    margin: 10px 0;
    font-family: Poppins;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #fff;
  }
  .list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    .item {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

@include mobile-only {
  .price {
    .list {
      display: block;
    }
  }
}
