@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.swapReserves {
  position: relative;
  padding: 20px;
  background: rgba(27, 13, 49, 0.7);
  &::before {
    content: '';
    position: absolute;
    inset: 0;
    padding: 1px;
    border-radius: 20px;
    background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.3) 25.9%, rgba(255, 255, 255, 0.1) 76.35%);
    -webkit-mask: linear-gradient(#fff, #fff) content-box, linear-gradient(#fff, #fff);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    z-index: 2;
  }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .header_title {
      font-family: Poppins;
      font-weight: 600;
      font-style: SemiBold;
      font-size: 20px;
      line-height: 30px;
      color: #fff;
    }

    .header_info {
      display: flex;
      align-items: center;
      .desc {
        margin-right: 12px;
        .name {
          font-family: Poppins;
          font-weight: 400;
          font-size: 12px;
          line-height: 18px;
          color: #a7a2c9;
        }
        .number {
          font-family: Poppins-Bold;
          font-weight: 900;
          font-size: 30px;
          line-height: 45px;
          color: #fff;
          text-align: right;
        }
      }
      .icon {
        width: 90px;
        height: 50px;
        img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .card {
    display: flex;
    margin-top: 20px;
    .card_item {
      position: relative;
      margin-right: 20px;
      flex: 1;
      padding: 14px 20px;
      background: rgba(42, 33, 70, 0.7);
      backdrop-filter: blur(10px);
      border-radius: 10px;
      overflow: hidden;
      &::after {
        z-index: -2;
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        width: 187px;
        height: 104px;
        background-image: url(../../../assets/images/swapAll/busd_2pool/busd_2pool_index1.png);
        background-repeat: no-repeat;
        background-size: auto 100%;
        background-position: right -30px bottom -30px;
        opacity: 0.08;
      }
      &:last-child {
        margin-right: 0;
      }
      .card_header {
        display: inline-flex;
        align-items: center;
        .card_name {
          margin-right: 10px;
          font-family: Poppins;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: rgba(167, 162, 201, 1);
        }
        .card_rate {
          padding: 0 4px;
          font-family: Poppins;
          font-weight: 400;
          font-size: 12px;
          height: 20px;
          line-height: 20px;
          color: rgba(167, 162, 201, 1);
          background: rgba(255, 255, 255, 0.1);
          border-radius: 4px;
        }
      }
      .card_number {
        margin-top: 10px;
        font-family: Poppins-Bold;
        font-weight: 900;
        font-size: 20px;
        line-height: 30px;
        color: #fff;
      }
    }
  }

  // pool bg start
