.swapReservesWrapLoading {
  .card {
    .card_item {
      &::after {
        display: none;
      }
    }
  }
}

.detail_contract {
  position: absolute;
  right: 0;
  top: 0;
  .detail_contract_item {
    margin-bottom: 24px;
    text-align: right;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
.detail_contract_m {
  display: none;
}

@include mobile-only {
  .swapReserves {
    padding: 10px;
    .header {
      display: block;
      .header_title {
        font-size: 16px;
        line-height: 24px;
      }

      .header_info {
        margin-top: 14px;
        flex-direction: row-reverse;
        justify-content: flex-end;
        .desc {
          margin-left: 10px;
          .number {
            font-size: 20px;
            line-height: 30px;
            text-align: left;
          }
        }
        .icon {
          width: 72px;
          height: 40px;
        }
      }
    }
    .card {
      display: block;
      margin-top: 14px;
      .card_item {
        margin-right: 0;
        border-radius: 10px;
        margin-bottom: 9px;
        &::after {
          width: 144px;
          height: 80px;
          background-position: right -10px bottom -10px;
        }
        &:last-child {
          margin-right: 0;
          margin-bottom: 0;
        }
        .card_header {
          .card_name {
            margin-right: 14px;
            font-size: 14px;
            line-height: 21px;
          }
        }
        .card_number {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
    .detail_wrap {
      margin-top: 14px;
    }
    .detail {
      .detail_item {
        margin-top: 10px;
        display: flex;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        .detail_label {
          margin-right: 6px;
          color: rgba(167, 162, 201, 1);
        }
        .detail_value {
          font-family: Poppins-Bold;
          font-weight: 600;
          color: rgba(255, 255, 255, 1);
        }
        .tipValue {
          margin-left: 6px;
        }
      }
    }
  }

  .detail_contract {
    display: none;
    position: relative;
    .detail_contract_item {
      margin-bottom: 0;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .detail_contract_m {
    display: inline-flex;
    flex-wrap: wrap;
    margin-top: 12px;
    margin-bottom: -14px;
    white-space: nowrap;

    .detail_contract_item {
      margin-right: 30px;
      margin-bottom: 14px;
      text-align: left;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
