@import '../../assets/css/var.scss';
@import '../../assets/css/mixins';

.swap {
  font-family: Poppins;
  // background: url(../../assets/images/swap/bg_swap.png) no-repeat;
  // background-position: 0 0;
  // background-size: 100% auto;
  .swapInner {
    margin: 0 auto;
    padding: 70px 0 0;
    width: 640px;
  }
  .hotTokenWrapper {
    position: relative;
    .hotTokenClose {
      z-index: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 50%;
      right: -38px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      cursor: pointer;
      background: #36254e;
      transform: translateY(-50%);
      &:hover {
        img {
          opacity: 1;
        }
      }
      img {
        display: block;
        width: 8px;
        height: 8px;
        opacity: 0.7;
      }
    }
  }
  .swapWrapper {
    // z-index: 3;
    position: relative;
    margin: 10px auto 0;
  }
  .swapReservesWrapper {
    margin: 30px auto 0;
  }
  .reservesWrapper {
    margin: 30px auto 0;
  }
  .priceWrapper {
    margin: 30px auto 0;
  }
  .psmWrapper {
    margin: 30px auto 0;
  }
}

@include mobile-only {
  .swap {
    background: transparent;
    .swapInner {
      padding: 0;
      width: 100%;
    }
    .hotTokenWrapper {
      .hotTokenClose {
        right: 10px;
      }
    }
    .swapWrapper {
      margin: 10px auto 0;
      padding: 0 15px;
    }
    .swapReservesWrapper {
      margin: 20px auto 0;
      padding: 0 15px;
    }
    .reservesWrapper {
      margin: 20px auto 0;
      padding: 0 15px;
    }
    .psmWrapper {
      margin: 20px auto 0;
      padding: 0 15px;
    }
    .priceWrapper {
      margin: 30px auto 0;
      padding: 0 15px;
    }
  }
}
