@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.item {
  display: flex;
  align-items: center;
  padding: 20px;
  background: rgba(27, 13, 49, 0.7);
  backdrop-filter: blur(10px);
  border-radius: 10px;
}
.token {
  flex: 1;
  width: 0;
  display: flex;
  align-items: center;
  .logo {
    margin-right: 10px;
    width: 40px;
    height: 40px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .info {
    flex: 1;
    width: 0;
    .name {
      margin-bottom: 3px;
      font-family: Poppins-Bold;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #fff;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .desc {
      display: inline-flex;
      align-items: center;
      .text {
        margin-right: 10px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: rgba(108, 102, 134, 1);
      }
      .icon {
        margin-right: 8px;
        width: 12px;
        height: 12px;
        &:last-child {
          margin-right: 0;
        }
        img {
          display: block;
          width: 100%;
          height: 100%;
          opacity: 0.7;
          cursor: pointer;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }
}
.charts {
  width: 250px;
  height: 80px;
}

@include mobile-only {
  .item {
    padding: 10px;
  }
  .token {
    flex: initial;
    width: 194px;
    margin-right: 10px;
    .info {
      .name {
        font-size: 14px;
        line-height: 20px;
      }
      .desc {
        .text {
          // width: 100px;
        }
        .icon {
          margin-right: 10px;
        }
      }
    }
  }
  .charts {
    flex: 1;
    width: 0;
    height: 80px;
  }
}
